@import "./styles/default.sass";
@import "./styles/myAnimation.sass";


body {
	padding-bottom: 100px; }

.element__img {
	display: block;
	margin: auto;
	max-height: 260px;
	max-width: 260px; }

.container.content {
	width: 1000px; }

a img {
	border: none; }

a:visited,
a:focus {
	color: #fff;
	text-decoration: none; }

img {
	font-size: 1em;
	color: #434343;
	-ms-interpolation-mode: bicubic;
	opacity: 1; }






